import { useEffect, useState, useCallback, useRef } from 'react'

// ì„¹ì…˜ ID ëª©ë¡ ì •ì˜
export const SECTION_IDS = [
  'hero',
  'about', 
  'experience',
  'skills',
  'projects',
  'contact',
  'books', // Books í˜ì´ì§€ ì¶”ê°€
  'projects-page', // Projects í˜ì´ì§€ ì¶”ê°€
  'experience-page', // Experience í˜ì´ì§€ ì¶”ê°€
  'about-page', // About í˜ì´ì§€ ì¶”ê°€
  'contact-page', // Contact í˜ì´ì§€ ì¶”ê°€
  'skills-page' // Skills í˜ì´ì§€ ì¶”ê°€
] as const

export type SectionId = typeof SECTION_IDS[number]

// ìŠ¤í¬ë¡¤ ìŠ¤íŒŒì´ í›… ì˜µì…˜ ì¸í„°í˜ì´ìŠ¤
interface UseScrollSpyOptions {
  /** í™œì„±í™” ê¸°ì¤€ì  ì˜¤í”„ì…‹ (í”½ì…€) - ê¸°ë³¸ê°’: 80px */
  offset?: number
  /** ë””ë°”ìš´ìŠ¤ ì§€ì—° ì‹œê°„ (ë°€ë¦¬ì´ˆ) - ê¸°ë³¸ê°’: 100ms */
  debounceDelay?: number
}

// ê¸°ë³¸ ì˜µì…˜
const DEFAULT_OPTIONS: Required<UseScrollSpyOptions> = {
  offset: 80,
  debounceDelay: 100
}

// ë””ë°”ìš´ìŠ¤ ìœ í‹¸ë¦¬í‹° í•¨ìˆ˜
const debounce = <T extends (...args: any[]) => any>(
  func: T,
  delay: number
): ((...args: Parameters<T>) => void) => {
  let timeoutId: number | null = null
  
  return (...args: Parameters<T>) => {
    if (timeoutId) {
      clearTimeout(timeoutId)
    }
    
    timeoutId = setTimeout(() => {
      func(...args)
    }, delay)
  }
}

// ìŠ¤í¬ë¡¤ ìŠ¤íŒŒì´ í›…
export const useScrollSpy = (options: UseScrollSpyOptions = {}) => {
  const { offset, debounceDelay } = { ...DEFAULT_OPTIONS, ...options }
  const [activeSection, setActiveSection] = useState<SectionId>('hero')
  const timeoutRef = useRef<number | null>(null)

  const handleScroll = useCallback(() => {
    // í˜„ì¬ ìŠ¤í¬ë¡¤ ìœ„ì¹˜ + ì˜¤í”„ì…‹ì„ í™œì„±í™” ê¸°ì¤€ì ìœ¼ë¡œ ì„¤ì •
    const activationPoint = window.scrollY + offset
    let newActiveSection: SectionId = 'hero' // ê¸°ë³¸ê°’

    // ì„¹ì…˜ë“¤ì„ ìˆœíšŒí•˜ë©° activationPointë¥¼ ì§€ë‚œ ê°€ì¥ ë§ˆì§€ë§‰ ì„¹ì…˜ì„ ì°¾ê¸°
    for (const sectionId of SECTION_IDS) {
      const element = document.getElementById(sectionId)
      if (!element) continue

      // ìš”ì†Œì˜ ë¬¸ì„œ ìƒë‹¨ìœ¼ë¡œë¶€í„°ì˜ ìœ„ì¹˜ (ì˜¤í”„ì…‹ ë°˜ì˜)
      const elementTop = element.offsetTop - offset
      
      // í˜„ì¬ ìŠ¤í¬ë¡¤ ìœ„ì¹˜ê°€ ì„¹ì…˜ ìƒë‹¨ë³´ë‹¤ í¬ê±°ë‚˜ ê°™ë‹¤ë©´, í•´ë‹¹ ì„¹ì…˜ì„ í™œì„±í™”
      if (activationPoint >= elementTop) {
        newActiveSection = sectionId
      }
    }
    
    setActiveSection(newActiveSection)
  }, [offset])

  // ë””ë°”ìš´ìŠ¤ëœ ìŠ¤í¬ë¡¤ í•¸ë“¤ëŸ¬
  const debouncedHandleScroll = useCallback(
    debounce(handleScroll, debounceDelay),
    [handleScroll, debounceDelay]
  )

  useEffect(() => {
    // ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ë“±ë¡
    window.addEventListener('scroll', debouncedHandleScroll, { passive: true })
    
    // ì´ˆê¸° ì‹¤í–‰ (ë””ë°”ìš´ìŠ¤ ì—†ì´ ì¦‰ì‹œ ì‹¤í–‰)
    handleScroll()

    // í´ë¦°ì—…
    return () => {
      window.removeEventListener('scroll', debouncedHandleScroll)
      // ë””ë°”ìš´ìŠ¤ íƒ€ì´ë¨¸ ì •ë¦¬
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current)
      }
    }
  }, [handleScroll, debouncedHandleScroll])

  return activeSection
}

// ë¶€ë“œëŸ¬ìš´ ìŠ¤í¬ë¡¤ í•¨ìˆ˜ (ì˜¤í”„ì…‹ ì§€ì›)
export const scrollToSection = (sectionId: SectionId, offset: number = 80) => {
  const element = document.getElementById(sectionId)
  if (element) {
    const elementTop = element.offsetTop - offset
    
    window.scrollTo({
      top: elementTop,
      behavior: 'smooth'
    })
  }
}
