import { useState, useEffect, useCallback, useMemo } from 'react'
import { skillsAPI } from '../services/api'
import { Skill } from '../types'
import { useSiteSettings } from './useSiteSettings'

/**
 * ì‚¬ì´ë“œë°”ì— í‘œì‹œí•  ìŠ¤í‚¬ ë°ì´í„°ë¥¼ ê´€ë¦¬í•˜ëŠ” ì»¤ìŠ¤í…€ í›…
 * 
 * @returns {Object} ìŠ¤í‚¬ ë°ì´í„° ë° ë¡œë”© ìƒíƒœ
 * @property {Skill[]} coreSkills - ì‚¬ì´ë“œë°”ì— í‘œì‹œí•  í•µì‹¬ ê¸°ìˆ 
 * @property {Skill[]} languageSkills - ì–¸ì–´ ì¹´ë“œì— í‘œì‹œí•  ì–¸ì–´ ìŠ¤í‚¬
 * @property {boolean} isLoadingSkills - ìŠ¤í‚¬ ë°ì´í„° ë¡œë”© ìƒíƒœ
 */
export const useSidebarSkills = () => {
  const [coreSkills, setCoreSkills] = useState<Skill[]>([])
  const [languageSkills, setLanguageSkills] = useState<Skill[]>([])
  const [isLoadingSkills, setIsLoadingSkills] = useState(false)
  const { settings: siteSettings } = useSiteSettings()

  // ìŠ¤í‚¬ ë°ì´í„°ë¥¼ ê°€ì ¸ì˜¤ëŠ” í•¨ìˆ˜ (useCallbackìœ¼ë¡œ ë©”ëª¨ì´ì œì´ì…˜)
  const fetchSkills = useCallback(async () => {
    setIsLoadingSkills(true)
    try {
      const response = await skillsAPI.getAll()
      const allSkills = response.data || []
      
      // ì‚¬ì´ë“œë°” í•µì‹¬ ê¸°ìˆ  í•„í„°ë§ (showInSidebar: true)
      let sidebarSkills = allSkills
        .filter((skill: Skill) => skill.showInSidebar)
        .sort((a: Skill, b: Skill) => a.order - b.order)
        .slice(0, siteSettings?.sidebarSkillCount || 4)
      
      // ì„ íƒëœ ìŠ¤í‚¬ì´ ì—†ìœ¼ë©´ ë ˆë²¨ ë†’ì€ ìˆœìœ¼ë¡œ fallback
      if (sidebarSkills.length === 0) {
        sidebarSkills = allSkills
          .sort((a: Skill, b: Skill) => b.level - a.level)
          .slice(0, siteSettings?.sidebarSkillCount || 4)
      }
      setCoreSkills(sidebarSkills)
      
      // ì–¸ì–´ ì¹´ë“œ ìŠ¤í‚¬ í•„í„°ë§ (showInLanguageCard: true)
      // ë ˆë²¨ ë†’ì€ ìˆœìœ¼ë¡œ ì •ë ¬ (ì™¼ìª½ì— ì˜í•˜ëŠ” ì–¸ì–´)
      let langSkills = allSkills
        .filter((skill: Skill) => skill.showInLanguageCard)
        .sort((a: Skill, b: Skill) => b.level - a.level)
        .slice(0, siteSettings?.languageCardSkillCount || 3)
      
      // ì„ íƒëœ ì–¸ì–´ ìŠ¤í‚¬ì´ ì—†ìœ¼ë©´ ë¹ˆ ë°°ì—´ ìœ ì§€ (ì–¸ì–´ ì¹´ë“œëŠ” ìˆ¨ê¹€)
      setLanguageSkills(langSkills)
    } catch (error) {
      console.error('ìŠ¤í‚¬ ë°ì´í„° ë¡œë“œ ì‹¤íŒ¨:', error)
      // ì—ëŸ¬ ì‹œ ë¹ˆ ë°°ì—´ë¡œ ì„¤ì •í•˜ì—¬ "ìŠ¤í‚¬ ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤" ë©”ì‹œì§€ í‘œì‹œ
      setCoreSkills([])
      setLanguageSkills([])
    } finally {
      setIsLoadingSkills(false)
    }
  }, [siteSettings?.sidebarSkillCount, siteSettings?.languageCardSkillCount])

  useEffect(() => {
    fetchSkills()
  }, [fetchSkills])

  // ë°˜í™˜ ê°ì²´ë¥¼ useMemoë¡œ ë©”ëª¨ì´ì œì´ì…˜í•˜ì—¬ ë¶ˆí•„ìš”í•œ ë¦¬ë Œë”ë§ ë°©ì§€
  return useMemo(() => ({
    coreSkills,
    languageSkills,
    isLoadingSkills
  }), [coreSkills, languageSkills, isLoadingSkills])
}

