import { useState, useEffect, useCallback } from 'react'
import { useAbortController } from './useAbortController'
import { useOptimisticArrayUpdate } from './useOptimisticUpdate'
import { useToastHelpers } from './useToast'
import { booksAPI } from '../services/api'
import { Book, BookFormData, BookUpdateData } from '../types'

/**
 * ë¦¬íŒ©í† ë§ëœ BookManager ë°ì´í„° ê´€ë¦¬ Hook
 * 
 * ì£¼ìš” ê°œì„ ì‚¬í•­:
 * 1. AbortControllerë¥¼ ì‚¬ìš©í•œ ë©”ëª¨ë¦¬ ëˆ„ìˆ˜ ë°©ì§€
 * 2. ë‚™ê´€ì  ì—…ë°ì´íŠ¸ë¡œ ì„±ëŠ¥ ê°œì„ 
 * 3. Toast ì•Œë¦¼ìœ¼ë¡œ ì‚¬ìš©ì í”¼ë“œë°± ê°œì„ 
 * 4. ì—ëŸ¬ ì²˜ë¦¬ ê°•í™”
 */
export function useBookManagerData() {
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const { createAbortController } = useAbortController()
  const { success, error: showError } = useToastHelpers()

  // ë‚™ê´€ì  ì—…ë°ì´íŠ¸ë¥¼ ì‚¬ìš©í•œ ì„œì  ë°ì´í„° ê´€ë¦¬
  const {
    data: books,
    isOptimistic,
    optimisticAdd,
    optimisticUpdateItem,
    optimisticDelete,
    resetData
  } = useOptimisticArrayUpdate<Book>([])

  // ì„œì  ë°ì´í„° ë¡œë“œ
  const fetchBooks = useCallback(async () => {
    try {
      setIsLoading(true)
      setError(null)
      
      const abortController = createAbortController()
      const response = await booksAPI.getAll()
      
        if (!abortController.signal.aborted) {
          resetData(response.data.data || [])
        }
    } catch (err) {
      if (!(err as Error).name?.includes('AbortError')) {
        const errorMessage = 'ì„œì  ëª©ë¡ì„ ë¶ˆëŸ¬ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.'
        setError(errorMessage)
        showError('ë¡œë“œ ì‹¤íŒ¨', errorMessage)
      }
    } finally {
      setIsLoading(false)
    }
  }, [createAbortController, resetData, showError])

  // ì„œì  ìƒì„±
  const createBook = useCallback(async (bookData: BookFormData) => {
    try {
      const newBook = {
        ...bookData,
        _id: `temp_${Date.now()}`,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString()
      } as Book

      const result = await optimisticAdd(
        newBook,
        () => booksAPI.create(bookData).then(res => res.data)
      )

      success('ì„œì  ì¶”ê°€ ì™„ë£Œ', `${bookData.title}ì´(ê°€) ì„±ê³µì ìœ¼ë¡œ ì¶”ê°€ë˜ì—ˆìŠµë‹ˆë‹¤.`)
      return result
    } catch (err) {
      showError('ì„œì  ì¶”ê°€ ì‹¤íŒ¨', err instanceof Error ? err.message : 'ì•Œ ìˆ˜ ì—†ëŠ” ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.')
      throw err
    }
  }, [optimisticAdd, success, showError])

  // ì„œì  ìˆ˜ì •
  const updateBook = useCallback(async (bookId: string, bookData: BookUpdateData) => {
    try {
      const result = await optimisticUpdateItem(
        bookId,
        bookData,
        () => booksAPI.update(bookId, bookData).then(res => res.data)
      )

      success('ì„œì  ìˆ˜ì • ì™„ë£Œ', 'ì„œì  ì •ë³´ê°€ ì„±ê³µì ìœ¼ë¡œ ìˆ˜ì •ë˜ì—ˆìŠµë‹ˆë‹¤.')
      return result
    } catch (err) {
      showError('ì„œì  ìˆ˜ì • ì‹¤íŒ¨', err instanceof Error ? err.message : 'ì•Œ ìˆ˜ ì—†ëŠ” ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.')
      throw err
    }
  }, [optimisticUpdateItem, success, showError])

  // ì„œì  ì‚­ì œ
  const deleteBook = useCallback(async (bookId: string) => {
    try {
      const result = await optimisticDelete(
        bookId,
        () => booksAPI.delete(bookId).then(() => {})
      )

      success('ì„œì  ì‚­ì œ ì™„ë£Œ', 'ì„œì ì´ ì„±ê³µì ìœ¼ë¡œ ì‚­ì œë˜ì—ˆìŠµë‹ˆë‹¤.')
      return result
    } catch (err) {
      showError('ì„œì  ì‚­ì œ ì‹¤íŒ¨', err instanceof Error ? err.message : 'ì•Œ ìˆ˜ ì—†ëŠ” ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.')
      throw err
    }
  }, [optimisticDelete, success, showError])

  // ì´ˆê¸° ë°ì´í„° ë¡œë“œ
  useEffect(() => {
    fetchBooks()
  }, [])

  return {
    books,
    isLoading,
    error,
    isOptimistic,
    createBook,
    updateBook,
    deleteBook,
    refetch: fetchBooks
  }
}

export default useBookManagerData