import { useState, useCallback } from 'react'

export type ToastType = 'success' | 'error' | 'warning' | 'info'

export interface Toast {
  id: string
  type: ToastType
  title: string
  message?: string
  duration?: number
}

interface UseToastReturn {
  toasts: Toast[]
  showToast: (toast: Omit<Toast, 'id'>) => void
  hideToast: (id: string) => void
  clearAllToasts: () => void
}

/**
 * Toast ì•Œë¦¼ ì‹œìŠ¤í…œì„ ìœ„í•œ Custom Hook
 * ì„±ê³µ, ì—ëŸ¬, ê²½ê³ , ì •ë³´ ë©”ì‹œì§€ë¥¼ í‘œì‹œí•  ìˆ˜ ìˆìŠµë‹ˆë‹¤.
 */
export function useToast(): UseToastReturn {
  const [toasts, setToasts] = useState<Toast[]>([])

  const showToast = useCallback((toast: Omit<Toast, 'id'>) => {
    const id = Math.random().toString(36).substr(2, 9)
    const newToast: Toast = {
      id,
      duration: 5000, // ê¸°ë³¸ 5ì´ˆ
      ...toast
    }

    setToasts(prev => [...prev, newToast])

    // ìë™ ì œê±° (durationì´ 0ì´ ì•„ë‹Œ ê²½ìš°)
    if (newToast.duration && newToast.duration > 0) {
      setTimeout(() => {
        hideToast(id)
      }, newToast.duration)
    }
  }, [])

  const hideToast = useCallback((id: string) => {
    setToasts(prev => prev.filter(toast => toast.id !== id))
  }, [])

  const clearAllToasts = useCallback(() => {
    setToasts([])
  }, [])

  return {
    toasts,
    showToast,
    hideToast,
    clearAllToasts
  }
}

/**
 * í¸ì˜ ë©”ì„œë“œë“¤ì„ ì œê³µí•˜ëŠ” Toast Hook
 */
export function useToastHelpers() {
  const { showToast, ...rest } = useToast()

  const success = useCallback((title: string, message?: string) => {
    showToast({ type: 'success', title, message })
  }, [showToast])

  const error = useCallback((title: string, message?: string) => {
    showToast({ type: 'error', title, message })
  }, [showToast])

  const warning = useCallback((title: string, message?: string) => {
    showToast({ type: 'warning', title, message })
  }, [showToast])

  const info = useCallback((title: string, message?: string) => {
    showToast({ type: 'info', title, message })
  }, [showToast])

  return {
    ...rest,
    success,
    error,
    warning,
    info
  }
}

export default useToast
