import { useQuery } from '@tanstack/react-query';
import { skillsAPI } from '../services/api';
import { SkillCategory } from '../types';

interface UseSkillsResult {
  skillCategories: SkillCategory[];
  loading: boolean;
  error: string | null;
  refetch: () => void;
}

// React Queryë¥¼ ì‚¬ìš©í•œ ë°ì´í„° í˜ì¹­
const fetchSkillCategories = async (): Promise<SkillCategory[]> => {
  const response = await skillsAPI.getCategories();
  // API ì‘ë‹µì´ { data: [...] } í˜•íƒœì¸ì§€ í™•ì¸
  const categories = response.data?.data || response.data || [];
  return categories;
};

export const useSkills = (): UseSkillsResult => {
  const {
    data: skillCategories = [],
    isLoading: loading,
    error,
    refetch
  } = useQuery({
    queryKey: ['skillCategories'],
    queryFn: fetchSkillCategories,
    staleTime: 5 * 60 * 1000, // 5ë¶„
    gcTime: 10 * 60 * 1000, // 10ë¶„
  });

  return { 
    skillCategories, 
    loading, 
    error: error ? 'ìŠ¤í‚¬ ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.' : null, 
    refetch 
  };
};
