import { useState, useCallback, useRef } from 'react'

interface OptimisticUpdateOptions<T> {
  onSuccess?: (data: T) => void
  onError?: (error: Error, rollback: () => void) => void
  onRollback?: (data: T) => void
}

/**
 * ë‚™ê´€ì  ì—…ë°ì´íŠ¸ë¥¼ ê´€ë¦¬í•˜ëŠ” Hook
 * API ìš”ì²­ ì „ì— UIë¥¼ ë¨¼ì € ì—…ë°ì´íŠ¸í•˜ì—¬ ë” ë‚˜ì€ ì‚¬ìš©ì ê²½í—˜ì„ ì œê³µí•©ë‹ˆë‹¤.
 * ì‹¤íŒ¨ ì‹œ ìë™ìœ¼ë¡œ ë¡¤ë°±í•©ë‹ˆë‹¤.
 */
export function useOptimisticUpdate<T>(
  initialData: T,
  options: OptimisticUpdateOptions<T> = {}
) {
  const [data, setData] = useState<T>(initialData)
  const [isOptimistic, setIsOptimistic] = useState(false)
  const previousDataRef = useRef<T>(initialData)

  const { onSuccess, onError, onRollback } = options

  // ë‚™ê´€ì  ì—…ë°ì´íŠ¸ ì‹¤í–‰
  const optimisticUpdate = useCallback(async (
    optimisticData: T | ((prev: T) => T),
    apiCall: () => Promise<T>
  ) => {
    // ì´ì „ ë°ì´í„° ë°±ì—…
    previousDataRef.current = data
    
    // ë‚™ê´€ì  ì—…ë°ì´íŠ¸ ì‹¤í–‰
    setData(prev => {
      const newData = typeof optimisticData === 'function' 
        ? (optimisticData as (prev: T) => T)(prev)
        : optimisticData
      return newData
    })
    
    setIsOptimistic(true)

    try {
      // API í˜¸ì¶œ
      const result = await apiCall()
      
      // ì„±ê³µ ì‹œ ì‹¤ì œ ë°ì´í„°ë¡œ ì—…ë°ì´íŠ¸
      setData(result)
      setIsOptimistic(false)
      onSuccess?.(result)
      
      return result
    } catch (error) {
      // ì‹¤íŒ¨ ì‹œ ë¡¤ë°±
      setData(previousDataRef.current)
      setIsOptimistic(false)
      
      const rollback = () => {
        setData(previousDataRef.current)
        onRollback?.(previousDataRef.current)
      }
      
      onError?.(error as Error, rollback)
      throw error
    }
  }, [data, onSuccess, onError, onRollback])

  // ë°ì´í„° ì—…ë°ì´íŠ¸ (ë‚™ê´€ì  ì—…ë°ì´íŠ¸ ì—†ì´)
  const updateData = useCallback((newData: T | ((prev: T) => T)) => {
    setData(prev => {
      const updated = typeof newData === 'function' 
        ? (newData as (prev: T) => T)(prev)
        : newData
      return updated
    })
  }, [])

  // ë°ì´í„° ë¦¬ì…‹
  const resetData = useCallback((newData: T) => {
    setData(newData)
    previousDataRef.current = newData
    setIsOptimistic(false)
  }, [])

  return {
    data,
    isOptimistic,
    optimisticUpdate,
    updateData,
    resetData
  }
}

/**
 * ë°°ì—´ ë°ì´í„°ë¥¼ ìœ„í•œ ë‚™ê´€ì  ì—…ë°ì´íŠ¸ Hook
 * ì¶”ê°€, ìˆ˜ì •, ì‚­ì œ ì‘ì—…ì— íŠ¹í™”ë˜ì–´ ìˆìŠµë‹ˆë‹¤.
 */
export function useOptimisticArrayUpdate<T extends { _id: string }>(
  initialData: T[],
  options: OptimisticUpdateOptions<T[]> = {}
) {
  const {
    data,
    isOptimistic,
    optimisticUpdate,
    updateData,
    resetData
  } = useOptimisticUpdate<T[]>(initialData, options)

  // í•­ëª© ì¶”ê°€
  const optimisticAdd = useCallback(async (
    newItem: T,
    apiCall: () => Promise<T>
  ) => {
    return optimisticUpdate(
      (prev) => [...prev, { ...newItem, _id: `temp_${Date.now()}` } as T],
      async () => {
        const result = await apiCall()
        // ì„ì‹œ IDë¥¼ ì‹¤ì œ IDë¡œ êµì²´
        updateData(prev => prev.map(item => 
          item._id.startsWith('temp_') ? result : item
        ))
        return [...data.filter(item => !item._id.startsWith('temp_')), result]
      }
    )
  }, [optimisticUpdate, updateData, data])

  // í•­ëª© ìˆ˜ì •
  const optimisticUpdateItem = useCallback(async (
    id: string,
    updates: Partial<T>,
    apiCall: () => Promise<T>
  ) => {
    return optimisticUpdate(
      (prev) => prev.map(item => 
        item._id === id ? { ...item, ...updates } : item
      ),
      async () => {
        const result = await apiCall()
        updateData(prev => prev.map(item => 
          item._id === id ? result : item
        ))
        return data.map(item => item._id === id ? result : item)
      }
    )
  }, [optimisticUpdate, updateData, data])

  // í•­ëª© ì‚­ì œ
  const optimisticDelete = useCallback(async (
    id: string,
    apiCall: () => Promise<void>
  ) => {
    const itemToDelete = data.find(item => item._id === id)
    if (!itemToDelete) return

    return optimisticUpdate(
      (prev) => prev.filter(item => item._id !== id),
      async () => {
        await apiCall()
        return data.filter(item => item._id !== id)
      }
    )
  }, [optimisticUpdate, data])

  // í•­ëª© ì¬ì •ë ¬
  const optimisticReorder = useCallback(async (
    reorderedItems: T[],
    apiCall: () => Promise<T[]>
  ) => {
    return optimisticUpdate(
      reorderedItems,
      async () => {
        const result = await apiCall()
        return result
      }
    )
  }, [optimisticUpdate])

  return {
    data,
    isOptimistic,
    optimisticAdd,
    optimisticUpdateItem,
    optimisticDelete,
    optimisticReorder,
    updateData,
    resetData
  }
}

export default useOptimisticUpdate
